    separador
    #t_2_1.titulo-segundo.color-acento-contenido
       h2 2.1 Instrumentacin primaria

    p Es la tecnologa que permite medir magnitudes fsicas y convertirlas en seales elctricas, neumticas o hidrulicas para transmitirla a elementos de control fina, entre estos dispositivos se encuentran:

    .row
      .col-lg-7
        .list.mb-4(data-aos="fade-down")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/tema2/icon1.svg', alt='Imgen decorativa')
          .list__txt
            h4.mb-0.tv.margin-icon <span style='border-bottom: 1px dotted #3076B6'>Analizadores</span>


      p Son dispositivos que permiten realizar la medicin de variables fsicas, algunas de estas variables son:

    .row.justify-content-center.align-items-center
      .col-lg-4.mb-4
        figure
          img(src='@/assets/curso/temas/tema2/img8.png', alt='Imgen decorativa')

      .col-lg-8
        LineaTiempoD.color-acento-contenido
          .row(numero="1" titulo="Cloro residual")
            .col-md-3
              figure
                img(src='@/assets/curso/temas/tema2/img9.png', alt='Imgen decorativa')

            .col-md-6.mb-4.mb-md-0
              p.text-small Existen diferentes tipos de analizadores: el amperimtrico, el voltamtrico, el polarogrfico, el potenciomtrico y el colorimtrico, unos miden solo cloro total, otro cloro libre y/o combinado como el amperimtrico.


          .row(numero="2" titulo="pH")
            .col-md-3
              figure
                img(src='@/assets/curso/temas/tema2/img10.png', alt='Imgen decorativa')

            .col-md-6.mb-4.mb-md-0
              p.text-small El medidor de pH incluye un electrodo de proceso y un electrodo combinado para referencia. Debe considerarse como el parmetro ms importante para caracterizar y controlar los procesos de tratamiento, por lo cual debe disponerse de un sistema de medida del pH eficiente y que sea capaz de detectar pequeas variaciones.

          .row(numero="3" titulo="Turbiedad")
            .col-md-3
              figure
                img(src='@/assets/curso/temas/tema2/img11.png', alt='Imgen decorativa')

            .col-md-6.mb-4.mb-md-0
              p.text-small La medicin continua de la turbiedad por la falta de transparencia del agua debido a la presencia de partculas extraas debe realizarse por medio de analizadores amperimtricos de turbiedad tal como el equipo o Unidad Nefelomtrica de Turbiedad (UNT); otros son los analizadores pticos que deben usarse cuando se tienen concentraciones medias de slidos. Debe tenerse en cuenta que la unidad de medicin actual es la UNT. Cuando se trata de evaluar pequeos valores de turbiedad es preferible el uso de turbidmetros de diseminacin.

          .row(numero="4" titulo="Aluminio residual")
            .col-md-3
              figure
                img(src='@/assets/curso/temas/tema2/img12.png', alt='Imgen decorativa')

            .col-md-6.mb-4.mb-md-0
              p.text-small El aluminio residual es una variable importante que debe determinarse en las plantas de tratamiento por los presuntos efectos sobre la salud humana (enfermedades renales) que produce el mantener altos contenidos de aluminio en la red de distribucin y los problemas de corrosin de las tuberas. 

          .row(numero="5" titulo="Monitor de corriente de reestabilizacin:")
            .col-md-3
              figure
                img(src='@/assets/curso/temas/tema2/img13.png', alt='Imgen decorativa')

            .col-md-6.mb-4.mb-md-0
              p.text-small Pueden utilizarse <i>Streaming Current Monitors</i> (SCM) los cuales son monitores de corriente de reestabilizacin de los coloides presentes en el agua para evaluar en continuo el resultado de la adicin de un coagulante sobre partculas coloidales durante el proceso de coagulacin. Este resultado se caracteriza por la medicin de una corriente electrocintica generada entre dos electrodos en una muestra de agua cruda a la cual se le aadi un coagulante. Esta corriente es generada por el reacomodamiento de los iones que se han extrado de las partculas adsorbidas sobre las paredes de una celda de medicin.


    .row.mt-5
      .col-lg-7
        .list.mb-3(data-aos="fade-down")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/tema2/icon1.svg', alt='Imgen decorativa')
          .list__txt
            h4.mb-0.tv.margin-icon <span style='border-bottom: 1px dotted #3076B6'>Medidores de flujo</span>

    .row.justify-content-center.align-items-center
      .col-lg-7
        p Para la medicin del caudal debe hacerse uso de medidores segn sea el tipo de caudal volumtrico o msico deseado. Debe adquirirse el conocimiento bsico de los tipos de fluxmetros volumtricos disponibles para realizar la mejor eleccin, entre los cuales se encuentran fluxmetros magnticos, ultrasnicos, de turbina, medidores Venturi y flujo de tubo, canaleta Parshall y vertederos, los cuales determinan el caudal directamente (desplazamiento) o indirectamente por deduccin (presin diferencial, rea variable, velocidad, fuerza, tensin inducida, torbellino). 

        .cajon.cajon-tema2.p-4.mt-3
          p.mb-0 Las mediciones de caudal msico deben efectuarse a partir de una medida volumtrica, compensndola para las variaciones de densidad del fluido o determinando directamente la caudal masa aprovechando caractersticas medibles de la masa, del fluido.

      .col-lg-5.mt-3
        figure
          img(src='@/assets/curso/temas/tema2/img14.png', alt='Imgen decorativa')

    .row.mt-5
      .col-lg-7
        .list.mb-3(data-aos="fade-down")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/tema2/icon1.svg', alt='Imgen decorativa')
          .list__txt
            h4.mb-0.tv.margin-icon <span style='border-bottom: 1px dotted #3076B6'>Medidores de nivel</span>

    .row.justify-content-center.align-items-center
      .col-lg-5
        figure
          img(src='@/assets/curso/temas/tema2/img15.png', alt='Imgen decorativa')

      .col-lg-7.mt-3
        p La medicin del nivel debe ser muy cuidadosa para asegurar el funcionamiento correcto de cada proceso y obtener el balance adecuado de materias primas o del efluente de la planta.

        p Debe tenerse el respectivo conocimiento de los tipos de medidores de nivel, entre los cuales  se encuentran los que miden directamente la altura del lquido sobre una lnea de referencia, medidores de presin hidrosttica, dispositivos que miden el desplazamiento producido en un flotador por el propio lquido contenido en el tanque del proceso y los medidores que aprovechan las caractersticas elctricas del lquido.

    .row.justify-content-center.align-items-center.mt-3
      .col-lg-8

        .cajon.cajon-tema2.p-4.mt-3
          p.mb-0 En los niveles de complejidad del sistema bajo y medio es conveniente utilizar medidores como la sonda, cinta y plomada, nivel de cristal e instrumentos de flotador. En los niveles de complejidad del sistema medio alto y alto, es conveniente utilizar medidores manmetricos, de membrana, de presin diferencial de diafragma, de desplazamiento a barra de torsin, conductiva, capacitiva, ultrasnica y de radiacin.

      .col-lg-4.mt-3
        figure
          img(src='@/assets/curso/temas/tema2/img16.svg', alt='Imgen decorativa')

    .row.mt-5
      .col-lg-7
        .list.mb-3(data-aos="fade-down")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/tema2/icon1.svg', alt='Imgen decorativa')
          .list__txt
            h4.mb-0.tv.margin-icon <span style='border-bottom: 1px dotted #3076B6'>Medidores de presin</span>

    .row.justify-content-center.align-items-center
      .col-lg-6
        figure
          img(src='@/assets/curso/temas/tema2/img17.png', alt='Imgen decorativa')

      .col-lg-6
        p.mt-3 Los instrumentos de presin deben estar sujetos a los siguientes grupos:

        .row 
          .col-lg-6
            ul.lista-ul--color.lista-ul--separador.fa-ul2.mb-3
              li.l-p
                i.fas.fa-check-circle.color-vineta2
                | Mecnicos
              li.l-p
                i.fas.fa-check-circle.color-vineta2
                | Neumticos

          .col-lg-6
            ul.lista-ul--color.lista-ul--separador.fa-ul2.mb-3
              li.l-p
                i.fas.fa-check-circle.color-vineta2
                | Electromecnicos
              li.l-p
                i.fas.fa-check-circle.color-vineta2
                | Electrnicos


        .cajon.cajon-tema2.p-4.mt-3
          p.mb-0 Deben aplicarse medidores de presin a lneas o procesos que se encuentren encerrados tales como sistemas de distribucin de aire comprimido, lneas de descarga de bombas y tanques, para esta finalidad se encuentran manmetros, tubos manomtricos Bourdon, elemento en espiral, helicoidal, fuelles, medidores de presin diferencial de diafragma, medidores capacitivos y ultrasnicos.

    .row.justify-content-center.mb-5.mt-5
      .col-xl-4.col-lg-7.col-md-9.col-11.mb-4.mb-xl-0
        .crd_hover_txt(data-aos="flip-left")
          .crd_hover_txt--img
            figure
              img(src="@/assets/curso/temas/tema2/img19.png", alt="Imgen decorativa")
          .crd_hover_txt--body
            h4.mb-3.text-center.text-white Transmisores
            p.mb-0.text-center.text-white El sistema de transmisin de seales debe sujetarse a las caractersticas propias de la planta, el nivel de complejidad del sistema y el tipo de seal a transmitir. 

      .col-xl-4.col-lg-7.col-md-9.col-11.mb-4.mb-xl-0
        .crd_hover_txt2(data-aos="flip-left")
          .crd_hover_txt--img
            figure
              img(src="@/assets/curso/temas/tema2/img20.png", alt="Imgen decorativa")
          .crd_hover_txt--body
            h4.mb-4.text-center.text-white Controladores
            p.mb-0.text-center.text-white Estos dispositivos deben comparar la variable controlada con el valor deseado y ejercer una accin correctiva de acuerdo con la desviacin. La variable controlada la pueden recibir directamente, como controladores locales o bien indirectamente en forma de seal neumtica o electrnica procedente de un transmisor.

    .row.mt-5
      .col-lg-7
        .list.mb-3(data-aos="fade-down")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/tema2/icon1.svg', alt='Imgen decorativa')
          .list__txt
            h4.mb-0.tv.margin-icon <span style='border-bottom: 1px dotted #3076B6'>Elementos de control final</span>

      p Despus de los instrumentos de sensibilidad son necesarios los sistemas de control final, los cuales deben permitir manipular y cambiar las condiciones del proceso para mantenerlas dentro del campo de medida deseado, estos elementos reciben la seal del controlador y modifica el caudal del fluido o agente de control. Estos son:
    
    .row.justify-content-center.align-items-center
      .col-lg-4
        figure
          img(src='@/assets/curso/temas/tema2/img21.png', alt='Imgen decorativa')

      .col-lg-8.mt-3
        TabsA.color-pestana-azul.mb-5        
          .tarjeta.clr-paleYellow.p-4(titulo="Actuadores elctricos")
            p Deben usarse estos dispositivos para controlar la posicin de vlvulas y compuertas que ajustan el caudal dentro de un proceso de control continuo o discreto. 

          .tarjeta.clr-paleYellow.p-4(titulo="Actuadores hidrulicos y neumticos")
            p Los usos que deben darse a estos actuadores son similares a los actuadores elctricos, con la diferencia de que estos operan en una forma diferente, lo cual se ve reflejado en los costos de operacin. 

          .tarjeta.clr-paleYellow.p-4(titulo="Motores de velocidad variable")
            p El tipo de motor debe escogerse de acuerdo con las condiciones de trabajo a las cuales se va a someter, la disponibilidad de espacio y la disponibilidad econmica de la planta. La velocidad debe adecuarse para las condiciones del proceso particular. 

          .tarjeta.clr-paleYellow.p-4(titulo="Vlvulas de control")
            p Deben tenerse vlvulas como elementos de control final para regular o limitar la variable controlable. Para la seleccin de la vlvula deben analizarse: los tipos para que sean compatibles, acoplarse a los requerimientos y cadas de presin, la rapidez y facilidad para ser operada, temperatura y finalmente compatibilidad con el fluido del proceso.

    .row.justify-content-center.align-items-center.mt-5
      .col-lg-10
        .cajon.color-gris.p-4.mb-4
          p.mb-0 <b>Nota:</b> importante para tener en cuenta, dentro de las vlvulas de control es necesario verificar el tipo de conexin realizada entre la vlvula y la tubera del proceso. Entre los diferentes tipos de vlvulas deben acondicionarse principalmente las siguientes: 

        .row
          .col-lg-8
            .row 
              .col-lg-6
                ul.lista-ul--color.lista-ul--separador.fa-ul2.mb-3
                  li.l-p
                    i.fas.fa-check-circle.color-vineta2
                    | Vlvulas de globo.
                  li.l-p
                    i.fas.fa-check-circle.color-vineta2
                    | Vlvulas de mariposa.
                  li.l-p
                    i.fas.fa-check-circle.color-vineta2
                    | Vlvulas de bola.

              .col-lg-6
                ul.lista-ul--color.lista-ul--separador.fa-ul2.mb-3
                  li.l-p
                    i.fas.fa-check-circle.color-vineta2
                    | Vlvulas de diafragma.
                  li.l-p
                    i.fas.fa-check-circle.color-vineta2
                    | Vlvulas tapn.


      .col-8.col-lg-2
        figure
          img(src='@/assets/curso/temas/tema2/img22.png', alt='Imgen decorativa')

    .row.mt-5
      .col-lg-7
        .list.mb-3(data-aos="fade-down")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/tema2/icon1.svg', alt='Imgen decorativa')
          .list__txt
            h4.mb-0.tv.margin-icon <span style='border-bottom: 1px dotted #3076B6'>Sistemas de registro</span>

      p La informacin recogida por los instrumentos debe clasificarse dentro de una de las siguientes categoras: 

    .bg_img_01
      .row.justify-content-center.mb-5
        .col-lg-4.mb-4.mb-lg-0
          .crd.crd--grayGrad(data-aos="flip-left")
            .bottomCircle.mb-5
              figure
                img(src="@/assets/curso/temas/tema2/img24.svg", alt="Imgen decorativa").img90.m-auto
            h4.text-center <i>Datos de proceso</i>
            p.text-center.mb-0 Debe mantenerse la operacin de la planta dentro de los lmites aceptables y alertar al personal de la planta cuando estos parmetros se encuentran fuera de las condiciones lmite.

        .col-lg-4.mb-4.mb-lg-0
          .crd.crd--grayGrad(data-aos="flip-left")
            .bottomCircle.mb-5
              figure
                img(src="@/assets/curso/temas/tema2/img25.svg", alt="Imgen decorativa").img90.m-auto
            h4.text-center <i>Informacin tcnica</i>
            p.text-center.mb-0 Debe utilizarse para proveer mejoras, tratando de optimizar cada sistema de tratamiento para aumentar el nivel de eficiencia y disminuir costos.

        .col-lg-4.mb-4.mb-lg-0
          .crd.crd--grayGrad(data-aos="flip-left")
            .bottomCircle.mb-5
              figure
                img(src="@/assets/curso/temas/tema2/img26.svg", alt="Imgen decorativa").img90.m-auto
            h4.text-center <i>Datos histricos</i>
            p.text-center.mb-0 Deben determinar inclinaciones de trminos a largo plazo con nfasis en la calidad tanto de la planta como del efluente y optimizacin de la planta, lo cual incide en planeaciones futuras. 

    separador
    #t_2_2.titulo-segundo.color-acento-contenido
       h2 2.2 Vlvulas, motobombas y tableros elctricos en el proceso de potabilizacin

    p <b>Atendiendo el RAS (2000), en su ttulo B</b>, define que las vlvulas deben cumplir con ciertas especificaciones entre los cuales se encuentran las que se describen a continuacin.

    .row.mt-5
      .col-lg-7
        .list.mb-3(data-aos="fade-down")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/tema2/icon1.svg', alt='Imgen decorativa')
          .list__txt
            h4.mb-0.tv.margin-icon <span style='border-bottom: 1px dotted #3076B6'>Materiales para vlvulas</span>

    .row.justify-content-center.align-items-center
      .col-8.col-lg-2.mt-3
        figure
          img(src='@/assets/curso/temas/tema2/img27.png', alt='Imgen decorativa')
      .col-lg-10.mt-3
        p El diseo debe asegurar que los materiales con los cuales se construyen las vlvulas, tanto en su cuerpo como en su mecanismo de cierre, cumplan con todas las especificaciones tcnicas reconocidas tanto a nivel nacional como internacional, en funcin de las caractersticas del agua, tales como el grado de agresividad y otros, as como de las presiones de servicio ms los factores de seguridad requeridos. 

        .cajon.cajon-tema2.p-4.mt-3
          p.mb-0 Estas especificaciones para los materiales deben seguir lo establecido en las Normas Tcnicas Colombianas NTC, las normas ASTM, las normas ISO o las normas DIN correspondientes para cada tipo de vlvula. Los revestimientos internos para las vlvulas y sus mecanismos deben ajustarse a las normas tcnicas colombianas correspondientes o a la Norma AWWA C550. 

    .row.mt-5
      .col-lg-7
        .list.mb-3(data-aos="fade-down")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/tema2/icon1.svg', alt='Imgen decorativa')
          .list__txt
            h4.mb-0.tv.margin-icon <span style='border-bottom: 1px dotted #3076B6'>Cajas para vlvulas</span>

      p En todo proyecto de diseo de una conduccin nueva o de la ampliacin de una conduccin existente que incluya vlvulas, se debe especificar para cada vlvula las dimensiones de las respectivas cajas u obras civiles, de tal manera que se permita el acceso para labores de operacin y mantenimiento por parte del personal de la persona prestadora del servicio de acueducto.


    .row.mt-5
      .col-lg-7
        .list.mb-3(data-aos="fade-down")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/tema2/icon1.svg', alt='Imgen decorativa')
          .list__txt
            h4.mb-0.tv.margin-icon <span style='border-bottom: 1px dotted #3076B6'>Tipos de vlvulas</span>

    .row.justify-content-center.align-items-center.mt-3
      .col-lg-4
        figure
          img(src='@/assets/curso/temas/tema2/img28.png', alt='Imgen decorativa')

      .col-lg-8
        p.mt-3 El proceso de potabilizacin de agua, se requiere de vlvulas resistentes y que su tiempo de duracin sea largo, es por esta razn que se debe conocer los tipos de vlvulas, su funcionalidad, material y protocolos de uso, entre otras caractersticas. 

        p Entre las vlvulas se encuentran, las que se describen a continuacin: 

    .row.justify-content-center.align-items-center.mt-5
      .col-lg-8
        ol.lista-ol--cuadro.fa-ul2
          li 
            .lista-ol--cuadro__vineta
              span 1
            p.mb-0 <b>Vlvulas de corte o cierre:</b> las vlvulas de corte o cierre deben localizarse al comienzo de la lnea de conduccin y al final de esta si la presin es mayor que 50 m.c.a. Para todos los niveles de complejidad del sistema, se debe hacer un estudio de los transientes hidrulicos ocasionados por la operacin de las vlvulas en el sistema y el diseo debe incluir recomendaciones sobre su operacin.

        .cajon.cajon-tema2.p-4.mt-3
          p.mb-0 En aquellos casos en que la tubera presenta grandes desniveles debido a la topografa del terreno, se debe verificar la condicin del cierre de una vlvula de corte y sus caractersticas. 

      .col-lg-4.mt-3
        figure
          img(src='@/assets/curso/temas/tema2/img29.png', alt='Imgen decorativa')

    .row.justify-content-center.align-items-center.mt-5
      .col-lg-4
        figure
          img(src='@/assets/curso/temas/tema2/img30.png', alt='Imgen decorativa')

      .col-lg-8.mt-3
        p Las vlvulas de cierre o corte deben cumplir con las correspondientes normas tcnicas colombianas y si estas no existen con las Normas Tcnicas: American Water Works Association [AWWA], American Society for Testing and Materials [ASTM], Deutsches Institut fr Normung [DIN], International Organization for Standardization [ISO], American Petroleum Institute [API] o cualquier otra norma de reconocimiento internacional equivalente, previa aprobacin del Ministerio de Ambiente Vivienda y Desarrollo Territorial, para el caso de los niveles de complejidad del sistema bajo y medio o de la persona prestadora del servicio para los niveles de complejidad del sistema medio alto y alto. 

        p Adicionalmente, el diseo debe evaluar la necesidad de instalar vlvulas de corte intermedias a lo largo de la lnea de conduccin en sistemas de gravedad. El dimetro de la vlvula debe seleccionarse igual al dimetro de las tuberas de conduccin y debe verificarse para evitar problemas de cavitacin en el caso de que se tengan flujos con altas velocidades. 

    .row.justify-content-center.align-items-center.mt-5
      .col-lg-8
        ol.lista-ol--cuadro.fa-ul2
          li 
            .lista-ol--cuadro__vineta
              span 2
            p.mb-0 <b>Vlvulas de ventosa:</b> en los puntos altos de la lnea de conduccin deben colocarse ventosas con el fin de facilitar la salida del aire que eventualmente se acumula en la conduccin durante su funcionamiento o cuando se procede a su llenado o vaciado. De igual forma, cuando la tubera tenga una pendiente constante se debe colocar una vlvula ventosa cada 300 m. 

        .cajon.cajon-tema2.p-4.mt-3
          p.mb-0 Dichos dispositivos deben permitir tambin la entrada automtica de aire durante las operaciones de descarga de la tubera o cuando el caudal de agua se disminuye por causa de una rotura, de maniobras o de paradas en el flujo de la tubera. El diseo debe tener en cuenta los siguientes aspectos:

      .col-lg-4.mt-3
        figure
          img(src='@/assets/curso/temas/tema2/img31.png', alt='Imgen decorativa')

    .row.justify-content-center.align-items-center.mt-5
      .col-8.col-lg-4
        figure
          img(src='@/assets/curso/temas/tema2/img32.png', alt='Imgen decorativa')
      .col-lg-8.mt-3
        ul.lista-ul--color.lista-ul--separador.fa-ul2
          li.l-p
            i.fas.fa-check-circle.color-vineta1
            | Podr utilizarse una ventosa nica para atender la entrada y la salida de aire, siempre y cuando dicho dispositivo sea capaz de atender ambas funciones. 

          li.l-p
            i.fas.fa-check-circle.color-vineta1
            | Preferiblemente las vlvulas de ventosa deben ser bridadas segn las especificaciones de la norma AWWA y con cuerpo fundido en hierro dctil. Para los niveles de complejidad del sistema bajo y medio se pueden utilizar vlvulas de ventosa roscada; para los niveles de complejidad del sistema medio alto y alto, el uso de las vlvulas de ventosa roscada deber contar con la aprobacin previa de la persona prestadora del servicio de acueducto. Tipo de vlvula. 

          li.l-p
            i.fas.fa-check-circle.color-vineta1
            | Cuando en la conduccin se encuentre una vlvula de lnea, debe existir una ventosa aguas arriba o aguas abajo, dependiendo de la pendiente para la aireacin durante el llenado y descarga de esta. 


          li.l-p
            i.fas.fa-check-circle.color-vineta1
            | Las ventosas tendrn los siguientes dimetros mnimos: para tuberas con dimetro interno real menor o igual que 100 mm, el dimetro mnimo ser de 50 mm. Para tuberas con dimetro interno real mayor que 100 mm, el dimetro mnimo de las ventosas ser de 75 mm. En caso de que el diseo hidrulico incluya ventosas con dimetros inferiores a los antes establecidos, ste deber ser aprobado por la persona prestadora del servicio. 

          li.l-p
            i.fas.fa-check-circle.color-vineta1
            | El diseo del dimetro y ubicacin de las vlvulas de ventosa debe realizarse de forma integrada con el de las vlvulas de purga. 

          li.l-p
            i.fas.fa-check-circle.color-vineta1
            | Toda vlvula de ventosa debe poder aislarse de la tubera principal por medio de una vlvula de corte. 
      


    .row.justify-content-center.align-items-center.mt-5
      .col-lg-8
        ul.lista-ul--color.lista-ul--separador.fa-ul2
          li.l-p
            i.fas.fa-check-circle.color-vineta1
            | Cada ventosa debe estar protegida por una cmara de inspeccin accesible, con su respectivo drenaje de aguas y los respiraderos necesarios para garantizar el flujo de aire y estar completamente asegurada. 

          li.l-p
            i.fas.fa-check-circle.color-vineta1
            | Los dispositivos de entrada de aire deben localizarse de tal modo que no se introduzca agua extraa al sistema. Los dispositivos previstos deben instalarse de tal manera que sus aperturas se siten por lo menos un metro (1.0 m) por encima del nivel mximo de agua que pudiera acumularse en el sitio de la ventosa. 

          li.l-p
            i.fas.fa-check-circle.color-vineta1
            |  Deben disponerse puntos intermedios para la entrada de aire en la tubera cuando la lnea piezomtrica correspondiente a la carga de un tramo del conducto durante operaciones de mantenimiento y/o reparacin, se site por debajo de ste, de forma tal que cause problemas de discontinuidad en la columna lquida o problemas de posible colapso de la tubera por aplastamiento. 

          li.l-p
            i.fas.fa-check-circle.color-vineta1
            | Como dispositivos automticos para la entrada o salida de aire de las lneas de conduccin pueden utilizarse los siguientes: ventosas de doble efecto para la descarga del aire acumulado durante el llenado y durante la operacin normal de la conduccin y para la entrada en las operaciones de descarga de agua. Tubos verticales o chimeneas cuando su extremidad superior pueda situarse por encima de la lnea piezomtrica mxima para la entrada de aire. 

          li
            i.fas.fa-check-circle.color-vineta1
            p.mb-0 En el caso de que exista una vlvula intermedia en la conduccin, esta debe estar dotada de un paso lateral (<em>by pass</em>) de tal manera que permita el flujo de aire hacia la ventosa. Este paso lateral debe estar dotado de su propia vlvula de corte y se debe utilizar cuando:

        .row.justify-content-center.align-items-center.mb-5
          .col-lg-6
            .tarjeta-avatar-b.mb-5
              .tarjeta-avatar-b__img
                img(src='@/assets/curso/temas/tema2/img34.svg' alt='Imgen decorativa')
              .tarjeta.tarjeta--gris
                .p-2
                  p La vlvula intermedia se localiza en tramos descendentes de la tubera y su apertura no puede realizarse sin causar perjuicios a la estructura.

          .col-lg-6
            .tarjeta-avatar-b.mb-5
              .tarjeta-avatar-b__img
                img(src='@/assets/curso/temas/tema2/img34.svg' alt='Imgen decorativa')
              .tarjeta.tarjeta--gris
                .p-2
                  p La vlvula intermedia sea del tipo compuerta y la presin en el punto en que estuviese instalada cause un empuje superior a 20 KN. 


      .col-8.col-lg-4.mb-3
        figure
          img(src='@/assets/curso/temas/tema2/img33.png', alt='Imgen decorativa')

    .row.justify-content-center.align-items-center.mb-5
      ul.lista-ul--color.lista-ul--separador.fa-ul2
        li.l-p
          i.fas.fa-check-circle.color-vineta1
          | Las vlvulas de ventosa deben cumplir con normas tcnicas nacionales e internacionales, tales como la Norma Tcnica Colombiana correspondiente, la Norma AWWA C512, fabricadas bajo normas internacionales, de reconocimiento internacional, que le aplique y se demuestra mediante certificado de producto. 
        li
          i.fas.fa-check-circle.color-vineta1
          |  La localizacin de las vlvulas de ventosa a lo largo de la conduccin y su dimensionamiento debe considerar los requerimientos de evacuacin de aire presentados durante las operaciones de llenado de las tuberas que conformen la conduccin o la red de conducciones, analizando la posibilidad de bloqueo del flujo. 

    .row.justify-content-center.align-items-center.my-5
      ol.lista-ol--cuadro.fa-ul2
        li 
          .lista-ol--cuadro__vineta
            span 3
          p.mb-0 <b>Vlvulas de purga o desage:</b> en los puntos bajos de la tubera de conduccin o cada 350 m, deben colocarse vlvulas de desage o de limpieza. En estos casos deben tenerse en cuenta los siguientes aspectos:

    .row.justify-content-center.align-items-center.mb-3
      .col-lg-4.mb-3
        figure
          img(src='@/assets/curso/temas/tema2/img35.png', alt='Imgen decorativa')

      .col-lg-8   
        ul.lista-ul--color.lista-ul--separador.fa-ul2
          li.l-p
            i.fas.fa-check-circle.color-vineta1
            | La descarga debe permitir la eliminacin total del agua contenida en la tubera de conduccin. 
          li.l-p
            i.fas.fa-check-circle.color-vineta1
            | Se recomienda que el dimetro de la tubera de desage est entre 1/3 y 1/4 del dimetro de la tubera principal, con un mnimo de 100 mm para tuberas mayores que 100 mm. Para dimetros menores debe adoptarse el mismo dimetro de la tubera principal. 
          li.l-p
            i.fas.fa-check-circle.color-vineta1
            | El diseo de las vlvulas de purga debe realizarse de forma integrada con el de las vlvulas de ventosa. 
          li.l-p
            i.fas.fa-check-circle.color-vineta1
            | Cada vlvula debe estar protegida con una cmara de inspeccin accesible con su respectivo drenaje.
          li.l-p
            i.fas.fa-check-circle.color-vineta1
            | Si la velocidad de salida de la vlvula de purga es mayor que 3 m/s, debe colocarse una estructura de disipacin de energa. 
          li.l-p
            i.fas.fa-check-circle.color-vineta1
            | Si la velocidad de salida de la vlvula de purga es mayor que 3 m/s, debe hacerse un estudio de cavitacin. 
          li
            i.fas.fa-check-circle.color-vineta1
            | El dimensionamiento de la descarga debe realizarse teniendo en cuenta los siguientes puntos: 

        ol.lista-ol--cuadro.fa-ul2
          li 
            .lista-ol--cuadro__vineta
              span a
            | La obtencin de una velocidad mnima que sea compatible con la remocin del material sedimentado y las biopelculas en el interior de la tubera, durante por lo menos el primer minuto de descarga. 
          li 
            .lista-ol--cuadro__vineta
              span b
            | Que el tiempo mximo para descarga sea impuesto por las condiciones de operacin. 
          li 
            .lista-ol--cuadro__vineta
              span c
            | El caudal mximo permitido por el sistema de recepcin del agua descargada.

    .row.justify-content-center.align-items-center.mb-3
      .col-lg-8.mb-3
        ol.lista-ol--cuadro.fa-ul2.mb-4
          li 
            .lista-ol--cuadro__vineta
              span 4
            p.mb-0 <b>Vlvulas de cheque:</b> en el caso que la lnea de aduccin o de conduccin corresponda a la lnea de impulsin de un bombeo, el diseo debe contemplar el uso de vlvulas de cheque o retencin con el fin de evitar el retroceso del agua, con el consiguiente vaciado del conducto y sus posibles daos en las bombas o aplastamiento de la tubera. Las vlvulas de cheque sencillas o de doble accin deben cumplir con lo establecido en las normas tcnicas colombianas correspondientes o en su defecto con las Normas AWWA, C508, AWWA C515, o cualquier otra norma de reconocimiento internacional equivalente.

        .cajon.color-gris.p-4
          p.mb-0 El uso de las vlvulas de cheque en el diseo deber contar con la aprobacin previa de la persona prestadora del servicio. En el caso de que las vlvulas de cheque estn fabricadas utilizando aleaciones de cobre, estas deben cumplir con la Norma Tcnica Colombiana NTC 1762.

      .col-lg-4
        figure
          img(src='@/assets/curso/temas/tema2/img36.png', alt='Imgen decorativa')      

    
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-4.mb-3
        figure
          img(src='@/assets/curso/temas/tema2/img37.png', alt='Imgen decorativa')     

      .col-lg-8  
        ol.lista-ol--cuadro.fa-ul2.mb-3
          li 
            .lista-ol--cuadro__vineta
              span 5
            p.mb-0 <b>Vlvulas de proteccin contra golpe de ariete:</b> este tipo de vlvulas deben instalarse en tuberas de aduccin por bombeo, sometidas a riesgos de sobrepresiones por golpe de ariete, sobre la lnea de impulsin, con el fin de proteger las bombas y las tuberas correspondientes. Podrn utilizarse vlvulas de alivio de presin, caso en el cual, stas deben estar especificadas segn normas tcnicas nacionales o internacionales, de reconocimiento internacional, tales como la NTC correspondiente, la Norma AWWA C506, o normas ISO y EN.

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-8.mb-4
        ol.lista-ol--cuadro.fa-ul2.mb-3
          li 
            .lista-ol--cuadro__vineta
              span 6
            p.mb-0 <b>Vlvulas de flujo o paso anular:</b> se utilizan para la regulacin de caudales y pueden tener o no regulacin de presin. Las vlvulas con regulacin de presin cuentan con dispositivos para reducir la presin a la entrada de los tanques de almacenamiento y/o compensacin.

        .cajon.color-gris.p-4.mb-3
          p.mb-0 Las vlvulas sin regulacin de presin no tienen ese tipo de dispositivos y son utilizadas en puntos intermedios de las conducciones o en entradas a tanques con presiones bajas, siempre y cuando el flujo #[b sea unidireccional.] Estas vlvulas deben tener un obturador interior en forma de mbolo que se mueva axial y perpendicularmente al asiento, para dejar un paso anular al agua en cualquier posicin.

        p El diseo del sistema obturador debe ser tal que pueda desplazarse el mbolo sin problemas ante una posible incrustacin de slidos en el rgano disipador.

      .col-lg-4
        figure
          img(src='@/assets/curso/temas/tema2/img38.png', alt='Imgen decorativa')

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-4.mb-3
        figure
          img(src='@/assets/curso/temas/tema2/img39.png', alt='Imgen decorativa')
      
      .col-lg-8.mb-4
        ol.lista-ol--cuadro.fa-ul2.mb-3
          li 
            .lista-ol--cuadro__vineta
              span 7
            p.mb-0 <b>Vlvulas de cono o chorro hueco:</b> para el caso de las estructuras de descarga de las lneas de conduccin, con el objeto de vaciar la tubera o producir las velocidades necesarias para el desprendimiento de biopelculas o de depsitos inorgnicos al interior de la tubera en operaciones de lavado, el diseo debe contemplar la instalacin de vlvulas de cono o chorro hueco acompaadas de sus correspondientes estructuras de disipacin de energa y canales de descarga a los cuerpos receptores.

        .cajon.color-gris.p-4
          p.mb-0 Este tipo de vlvulas son generalmente usadas para descargar el agua de forma superficial a los canales y/o cuerpos receptores. Sin embargo, pueden ser instaladas bajo tierra cuando el nivel de agua, en la zona aguas abajo, se encuentre por encima del nivel de instalacin de la vlvula.

    .row.justify-content-center.align-items-center.mb-5
      p En este caso se debe tener una estructura de disipacin para airear el flujo y evitar inestabilidades hidrulicas. Para determinar el tamao de las vlvulas se deben tener en cuenta dos factores: el caudal mximo de descarga y la mnima altura disponible en la entrada de la vlvula. En caso de que las vlvulas de cono o chorro hueco tengan la posibilidad de descargar en forma sumergida, bajo cualquier condicin de operacin, el diseo debe tener en cuenta la posibilidad de ocurrencia de cavitacin y, por consiguiente, debe plantear las medidas de proteccin necesarias.


    separador

    #t_2_3.titulo-segundo.color-acento-contenido
       h2 2.3 Bombas

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8.mb-4
        p.mb-4 Son dispositivos que permiten bombear o impulsar el agua de un lugar a otro, usualmente su movimiento es ascendente; existen diferentes tipos de bombas, pero todas constan de un orificio de entrada o tambin denominado de aspiracin, y otro de salida o de impulsin. El nmero de bombas en la estacin de bombeo debe definirse desde la etapa de diseo de acuerdo con la capacidad requerida, las etapas de desarrollo y la energa disponible, al igual que estar sujeto a una evaluacin econmica. De todas formas, el nmero mnimo de bombas es dos, cada una con una capacidad igual al caudal de diseo de la estacin de bombeo.

        .cajon.color-gris.p-4
          p.mb-0 Cuando el nmero de bombas sea mayor que dos, la capacidad debe distribuirse equitativamente entre ellas. Adems, deben preverse unidades de reserva del mismo tipo; cuando se requieran tres o ms bombas, debe colocarse una unidad adicional como reserva por cada tres bombas empleadas.

      .col-lg-4
        figure
          img(src='@/assets/curso/temas/tema2/img40.png', alt='Imgen decorativa')      


    .row.mt-5
      .col-lg-7
        .list.mb-3(data-aos="fade-down")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/tema2/icon1.svg', alt='Imgen decorativa')
          .list__txt
            h4.mb-0.tv.margin-icon <span style='border-bottom: 1px dotted #3076B6'>Tipos de bombas</span>

    .row.justify-content-center.align-items-center.mb-3
      .col-lg-4.mb-3
        figure
          img(src='@/assets/curso/temas/tema2/img41.png', alt='Imgen decorativa')

      .col-lg-8
        p Desde la etapa de diseo, las bombas deben seleccionarse de forma tal que se obtenga la capacidad y la altura dinmica requeridas, establecidas por el punto de operacin el cual debe determinarse de acuerdo con las curvas caractersticas del sistema de bombeo y del sistema de tuberas de impulsin. 

        p El dimensionamiento y la seleccin del tipo de bombas debe realizarse en conjunto con la tubera de impulsin y con el tanque de almacenamiento de aguas abajo, buscando siempre la condicin de costo mnimo, incluyendo los costos iniciales, los costos de construccin, de operacin, de expansin y de mantenimiento. 

        .cajon.color-gris.p-4
          p.mb-0 Adicionalmente deben tenerse en cuenta #[b la Norma Tcnica NTC 1775 y las normas AWWA E101.]
          p.mb-0 El diseo debe tener en cuenta los siguientes criterios para la seleccin del tipo de bombas, veamos: 

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8
        ul.lista-ul--color.lista-ul--separador.fa-ul2
          li.l-p
            i.fas.fa-check-circle.color-vineta1
            | El espacio requerido para la instalacin de las bombas dentro de la estacin de bombeo. 
          li.l-p
            i.fas.fa-check-circle.color-vineta1
            | La forma de operacin prevista de las bombas, en serie o en paralelo. 
          li.l-p
            i.fas.fa-check-circle.color-vineta1
            | La variacin en los niveles mximo y mnimo en el pozo de succin y en la descarga. 
          li.l-p
            i.fas.fa-check-circle.color-vineta1
            | La variacin de los caudales bajo las diferentes condiciones de operacin hidrulica de la estacin de bombeo. 
          li.l-p
            i.fas.fa-check-circle.color-vineta1
            | El tiempo de operacin de las bombas.
          li.l-p
            i.fas.fa-check-circle.color-vineta1
            | La compatibilidad con otros equipos de bombeo existentes en el municipio.
          li
            i.fas.fa-check-circle.color-vineta1
            | El nivel del ruido generado por la operacin de las bombas.

      .col-lg-4
        figure
          img(src='@/assets/curso/temas/tema2/img42.svg', alt='Imgen decorativa')

    .row.mb-5
      .col-lg-7
        .list.mb-3(data-aos="fade-down")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/tema2/icon1.svg', alt='Imgen decorativa')
          .list__txt
            h4.mb-0.tv.margin-icon <span style='border-bottom: 1px dotted #3076B6'>Materiales de las bombas</span>

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-10
        .bloque-texto-g.color-acento-contenido.p-3.p-sm-4.p-md-4
          .bloque-texto-g__img(
            :style="{'background-image': `url(${require('@/assets/curso/temas/tema2/img43.png')})`}"
          )(alt="Imgen decorativa")
          .bloque-texto-g__texto.p-4
            p.mb-0 Los materiales de las bombas incluyendo los impulsores, las carcasas y otros componentes, deben ser seleccionados de acuerdo con las caractersticas del agua que va a bombearse y teniendo en cuenta la temperatura, la conductividad, la capacidad de corrosin y los slidos en suspensin.

    .row.justify-content-center.align-items-center.mb-5
      .cajon.color-gris.p-4
        p.mb-0 El material de las bombas y de los diferentes componentes debe resistir los efectos de corrosin que el agua pueda causar en ellos. Se pueden utilizar materiales como: hierro fundido, acero inoxidable, acero al carbn, acero fundido y hierro dctil. Para los accesorios de la bomba se puede usar latn y bronce. Todos los materiales utilizados deben estar certificados por normas tcnicas nacionales o internacionales.


    separador

    #t_2_4.titulo-segundo.color-acento-contenido
       h2 2.4 Sustancias qumicas para el tratamiento de agua


    .row.justify-content-center.align-items-center.mb-5
      .col-lg-5.mb-3
        figure
          img(src='@/assets/curso/temas/tema2/img44.png', alt='Imgen decorativa')

      .col-lg-7
        p #[b De acuerdo con el RAS 2000, ttulo C], las sustancias qumicas para el tratamiento de aguas ms utilizadas son los coagulantes y de estos, los que pueden emplearse son:

        ul.lista-ul--color.lista-ul--separador.fa-ul2
          li.l-p
            i.fas.fa-check-circle.color-vineta1
            | Coagulantes metlicos.
          li.l-p
            i.fas.fa-check-circle.color-vineta1
            | Coagulantes polmeros orgnicos e inorgnicos.


    .row.mb-5
      .col-lg-7
        .list.mb-3(data-aos="fade-down")
          .list__item
            .list__item__icon
              img(src='@/assets/curso/temas/tema2/icon1.svg', alt='Imgen decorativa')
          .list__txt
            h4.mb-0.tv.margin-icon <span style='border-bottom: 1px dotted #3076B6'>Coagulantes metlicos</span>

    .row.justify-content-center.align-items-center.mb-5
      p Para la dosificacin en la coagulacin por adsorcin-neutralizacin debe tenerse en cuenta la relacin estequiomtrica entre la dosis del coagulante y la concentracin de los coloides, ya que una sobredosis conduce a una reestabilizacin de las partculas. Para aguas con bajo nivel de alcalinidad, se recomienda aumentar el pH aadiendo hidrxido de calcio (Ca(OH)<sub>2</sub>).

      p.mb-5 Estos coagulantes metlicos son de dos tipos: 

      .row 
        .col-lg-6
          .bg2-fondo3.p-4(alt="Imgen decorativa")
            .row 
              .col-3
              .col-9.col-lg-8
                h4 Sales de aluminio
                p Los coagulantes con sales de aluminio son:

                ul.lista-ul--color.lista-ul--separador.fa-ul2
                  li.l-p
                    i.fas.fa-check-circle.color-vineta1
                    | Sulfato de aluminio.
                  li.l-p
                    i.fas.fa-check-circle.color-vineta1
                    | Sulfato de aluminio amoniacal.
                  li.l-p
                    i.fas.fa-check-circle.color-vineta1
                    | Aluminato de sodio.
                  li.l-p
                    i.fas.fa-check-circle.color-vineta1
