        p.mb-4  Se pueden emplear los polmeros de hierro (III) y aluminio (III) como coagulantes. Entre los polmeros orgnicos o polielectrolitos que se pueden emplear estn los polmeros naturales y los sintticos. Entre los primeros estn los derivados del almidn, la celulosa y los materiales proteicos. Para la aplicacin de los coagulantes sintticos debe solicitarse la aprobacin del Ministerio de Salud y Proteccin Social o verificar que haya sido aprobado su uso en el tratamiento del agua potable por la Comunidad Europea, por Estados Unidos o por Canad. 

        .cajon.color-gris.p-4
          p.mb-0 En la operacin, la velocidad de agitacin debe ser menor, ya que altas velocidades pueden llegar a romper las largas cadenas polimricas; por tanto, debe mantenerse una mezcla uniforme y se debe evitar la ruptura de los puentes formados entre polmeros y coloides. Para su empleo se recomienda la realizacin de ensayos de jarras en rangos amplios de dosificaciones y de bajas velocidades para determinar la dosificacin ptima.


    .row.justify-content-center.align-items-center.mb-5
