              p En este tipo de dosificadores la dosis requerida debe determinarse midiendo el volumen de material que libera una superficie que se desplaza a velocidad constante. Los dosificadores ms utilizados son la vlvula alveolar, el disco giratorio, el cilindro giratorio, el plato oscilante y de tornillo. El disco giratorio se recomienda para dosificar sulfato de aluminio, cal, carbonato de sodio o de calcio. Debe proveerse de agua de buena calidad para la preparacin de la solucin, lo mismo que una adecuada mezcla. 
          
