        p En el proceso de tratamiento de agua se utilizan diferentes productos qumicos que garantizan la efectividad de este proceso; sin embargo, se debe tener mucha claridad frente a la cantidad a dosificar y esto depende tambin de diversas variables como la calidad de agua, su caudal y el objetivo que se espera con el producto a dosificar. La mayora de estos productos son lquidos y se deben suministrar a travs de bombas dosificadoras, por lo que es necesario conocer el tipo de dosificador a utilizar.

