              p.mb-0 La caracterizacin del agua cruda a tratar debe realizarse sobre muestras representativas de por lo menos un ciclo hidrolgico anual, con el fin de analizar todas las posibles calidades de agua cruda que se puedan presentar histricamente durante ese periodo; es decir, cambios en la turbiedad, color, caractersticas microbiolgicas, demanda de cloro, temperatura, pH, alcalinidad, contenido de algas, hierro y manganeso, cambios en el sabor y olor, entre otros. De no ser esto posible, acudir a registros histricos de calidades de agua existentes en corporaciones regionales, secretaras de salud y sistemas que se abastezcan de las mismas fuentes.

